ma de difusión —basado en el número de me gusta y compartir que hace cada persona usuaria para cada publicación— permite conseguir una gran propagación con una inversión de bajo presupuesto y favorecer un marketing de contenidos y una cultura del compromiso (confianza hacia la marca) eficaces. A través de Facebook se puede enlazar videos o presentaciones que la tienda en línea haga en otras redes sociales o compartir videos de YouTube con la herramienta específica para Facebook que este sitio web pone a disposición de manera gratuita. Esta red social dispone de diversas utilidades en lo referente al uso de videos para promocionar productos, incentivar las ventas e incrementar la atracción hacia las tiendas en línea. (Hernández, 2018).")],1)]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Instagram","icono":require('@/assets/curso/img106.svg')}},[_c('p',[_c('b',[_vm._v("Instagram: ")]),_c('b-none',[_vm._v("es una red social basada en el impacto visual que facilita una amplia difusión del producto y potenciar el compromiso con las personas usuarias. Los datos recabados por analistas confirman que el uso de Instagram como herramienta en un comercio electrónico genera un grado de fidelización de más del 25% frente a otras herramientas disponibles. Instagram permite creación de videos y contenidos, permite desarrollar el denominado storytelling visual, una técnica basada en que las imágenes cuenten una historia por sí solas, para provocar emociones, sentimientos y experiencias intensas en las personas, una tendencia en auge al ser muchas las redes sociales basadas principalmente en el contenido visual. (Hernández, 2018).")])],1)]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"LinkedIn","icono":require('@/assets/curso/img107.svg')}},[_c('p',[_c('b',[_vm._v("LinkedIn: ")]),_c('b-none',[_vm._v("es una red social online profesional. Sus características y servicios ofrecidos se orientan a que los usuarios creen, mantengan o intensifiquen sus relaciones laborales y de negocio. De hecho, el perfil del usuario de LinkedIn tiene un formato similar al de un curriculum vitae, utilizándose la información registrada como base para la búsqueda de profesionales, proveedores, socios o inversores, entre otros. Asimismo, el usuario tiene la posibilidad de formar parte de distintos grupos de interés, documentando el intercambio de experiencias, la búsqueda y el ofrecimiento de consejos o la ayuda profesional. Estos grupos suelen responder a una temática sectorial, profesional o tecnológica, y son gestionados por el propio usuario. (Carballar, 2018).")]),_c('br'),_c('br'),_vm._v("Si se desea hacer publicidad de una empresa de forma más corporativa, LinkedIn es un gran aliado. Esta comunidad permite conocer y reclutar personas por medio de la visualización del currículo y la experiencia profesional que han decidido mostrar, así como crear grupos por sectores y ámbitos, y generar debates y corrientes a las que pueden unirse profesionales interesados en el tema propuesto. Para contactar con cualquier profesional, se requiere la autorización previa de las personas registradas, lo que fomenta su confianza como usuarias del servicio. Con la red de contactos se pueden crear grupos para compartir materiales fotográficos, videos o enlaces a otras webs. (Hernández, 2018).")],1)]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Twitter","icono":require('@/assets/curso/img108.svg')}},[_c('p',[_c('b',[_vm._v("Twitter: ")]),_c('b-none',[_vm._v("es un caso especial de red social, se comunica de forma unidireccional ya que todos pueden leer sus textos. Permite intercambiar textos planos; aunque es muy habitual que el texto contenga referencias (enlaces) a páginas web donde se amplía la información. Twitter se basa pues en que sus usuarios compartan información sobre lo que hacen, piensan, sienten o saben a partir de estos textos. (Carballar, 2018).")]),_c('br'),_c('br'),_vm._v("Su carácter conciso parece que hace más difícil la promoción de productos. Esta red social está especialmente diseñada para campañas y promociones breves que busquen un efecto inmediato. Se pueden compartir tanto videos nativos como videos en directo ya que la aplicación Periscope (desarrollada por Twitter) permite la retransmisión en tiempo real {streaming}. La opción de retwitear por parte del usuario facilita la viralización y el lanzamiento de campañas, conectar con potenciales clientes, etc., con apenas presupuesto o inversión en gastos publicitarios. (Hernández, 2018). ")],1)]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Pinterest","icono":require('@/assets/curso/img109.svg')}},[_c('p',[_c('b',[_vm._v("Pinterest: ")]),_c('b-none',[_vm._v("es lo que podríamos denominar una red social visual. Conecta a las personas a través de mostrar las cosas en las que estamos interesados a través de imágenes o videos. Mostrar la portada del libro que leemos, una imagen de nuestro lugar favorito o una foto de nuestro mejor guiso puede decir mucho de nosotros y convertirse en un motivo de intercambio y relación. (Carballar, 2018).")]),_c('br'),_c('br'),_vm._v("Herramienta muy poderosa para promocionar productos visuales. Muy recomendable en sectores de la moda, relojería, joyería, piedra natural, etc. Funciona prácticamente como un catálogo en línea y además permite incluir enlace a una web o tienda en línea. (Hernández, 2018).")],1)]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"YouTube ","icono":require('@/assets/curso/img110.svg')}},[_c('p',[_c('b',[_vm._v("YouTube ")]),_c('b-none',[_vm._v("está entre las páginas web más vistas del mundo. Los videos están grabados con todo tipo de dispositivos. Si el video necesita ser editado, Youtube dispone de herramientas que lo permite. Se pueden compartir nuestros videos (o los de otros) a través de cualquier medio de comunicación social en tiempo real en nuestra propia sala donde podremos interactuar con otros usuarios a través del chat. El resto de participantes también podrán compartir sus videos y hacer comentarios. Por otro lado, Youtube ofrece lo que se llama compartición activa (active sharing) muestra a los demás qué videos estamos viendo. (Carballar, 2018).")]),_c('br'),_c('br'),_vm._v("Se puede afirmar que esta red social es líder del videomarketing. Funciona como un gran catálogo o librería de videos, que permite compartir todo tipo de videos de una marca y difundir su enlace en otras redes sociales [incluso hay un formato especial para Facebook]. Hay que decir que, a pesar de su notoriedad por alojar gran parte de los videos en internet. (Hernández, 2018).")],1)])]),_c('br'),_c('br'),_vm._m(26),_vm._m(27),_c('p',{staticClass:"mb-5"},[_vm._v("Irureta (2015) clasifica los modelos de negocio electrónico de la siguiente forma teniendo en cuenta la relación comercial entre el cliente y el proveedor:")]),_vm._m(28),_vm._m(29),_vm._m(30),_vm._m(31)],1)],1)}
