oyamos y recibimos apoyo alrededor de un interés común. Todo esto nos lleva a que la base de los medios sociales es la relación. Los clientes no son números de una base de datos, ni compradores de un producto, sino personas con las que deseamos mantener una relación. En este entorno es importante que todos los componentes de la comunidad perciban que lo importante no es una simple transacción, sino mantener una relación de confianza y compromiso que haga que nos sigan prefiriendo frente a la competencia. (Carballar, 2018). "),_c('br'),_c('br'),_vm._v("Se debe trabajar en la captación de nuevos usuarios para registrarlos como clientes, no obstante, el máximo esfuerzo debe dirigirse a fidelizar clientes. La fidelización es la herramienta ideal para lograr que un cliente vuelva a comprar. Por tanto, establecer con los visitantes de la web relaciones duraderas constituirá un objetivo principal para cualquier comercio on-line. (Irureta, 2015)."),_c('br'),_c('br'),_vm._v("Las técnicas para marketing relacional son: establecer contacto a través de un e-mail, ofrecer descuentos para próximas compras o en aquellos productos que adquirió anteriormente, ofrecer un buen servicio de postventa, ponerse a su disposición y resolver sus posibles consultas, anotar sus preferencias, recordar al cliente que el comercio está abierto a cualquier sugerencia o pregunta, realizarle una encuesta de satisfacción y pedirles que se inscriban en el boletín electrónico gratuito. (Irureta, 2015).")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"spam-text p-3 mb-5"},[_c('p',{staticClass:"mb-0"},[_vm._v("Marketing de buscadores")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-2"},[_c('figure',{staticClass:"mb-5"},[_c('img',{attrs:{"src":require("@/assets/curso/img95.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-lg-10"},[_c('p',[_vm._v("Los buscadores son páginas web que ayudan a localizar información y proporcionan enlaces hacia las páginas que contienen dicha información. La mayoría de los clientes potenciales encuentran las tiendas on-line utilizando motores de búsqueda, por eso la forma más efectiva de dar a conocer un comercio web es por medio de los buscadores, utilizando las palabras y las frases adecuadas, un contenido interesante y cualquier otra estrategia que pueda posicionar la web en los primeros lugares de los motores de búsqueda. Por medio del alta en buscadores se consigue aparecer en las primeras posiciones de los resultados de búsqueda y aumentar el tráfico hacia nuestra página. Ej. Google es el buscador más utilizado, debido a la precisión con que extrae los resultados de búsqueda. Además, ofrece diferentes extras, como correo electrónico, aplicaciones de productividad e incluso pantallas personalizables. (Irureta, 2015)."),_c('br'),_c('br'),_vm._v("El SEO (Search Engine Optimization), también llamado posicionamiento natural, consiste en realizar ajustes en la web para lograr un mejor posicionamiento en la página de resultados de los motores de búsqueda. Así, a través de etiquetas indexadas, se consigue que el buscador traslade la página a las primeras posiciones. Para conseguirlo hay que tener en cuenta aspectos técnicos, semánticos y de marketing. (Irureta, 2015)."),_c('br'),_c('br'),_vm._v("Para Eva María Hernández (2018) existen 3 tipos de posicionamiento SEO.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tarjetas-ordenadas m-auto mb-5"},[_c('div',{staticClass:"tarjeta--boton color-secundario p-4 m-3 tj"},[_c('img',{staticClass:"m-auto mb-3",staticStyle:{"width":"80px"},attrs:{"src":require("@/assets/curso/img96.svg"),"alt":"Texto que describa la imagen"}}),_c('h4',{staticClass:"text-center"},[_vm._v("SEO interno (on-page)")]),_c('p',{staticClass:"text-center"},[_vm._v("E